.channel-list-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--bg-primary);
  transition: background-color 0.3s ease;
}

.channel-list-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 20px;
  border-bottom: 1px solid var(--border-color);
  background-color: var(--bg-primary);
  transition: background-color 0.3s ease, border-color 0.3s ease;

  h2 {
    margin: 0;
    font-size: 16px;
    font-weight: 700;
    color: var(--text-primary);
  }

  button {
    color: var(--slack-primary);
    
    &:hover {
      background-color: var(--slack-primary-light);
    }
  }
}

.channel-list {
  flex: 1;
  overflow-y: auto;
  padding: 8px 0;

  &::-webkit-scrollbar {
    width: 6px;
  }

  &::-webkit-scrollbar-track {
    background: var(--bg-secondary);
  }

  &::-webkit-scrollbar-thumb {
    background: var(--border-color);
    border-radius: 3px;

    &:hover {
      background: var(--text-muted);
    }
  }
}

.channel-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 20px;
  cursor: pointer;
  transition: all 0.2s ease;
  color: var(--text-secondary);
  border-left: 3px solid transparent;

  &:hover {
    background-color: var(--slack-primary-light);
    color: var(--text-primary);
  }

  &.active {
    background: var(--slack-primary-light);
    color: var(--slack-primary);
    border-left-color: var(--slack-primary);
    font-weight: 600;
  }

  .channel-icon {
    font-size: 20px;
    width: 20px;
    height: 20px;
    color: inherit;
  }

  .channel-info {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 0;
  }

  .channel-name {
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .unread-badge {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 22px;
    height: 22px;
    padding: 0 6px;
    border-radius: 11px;
    background: var(--slack-gradient);
    color: white;
    font-size: 11px;
    font-weight: 700;
    box-shadow: 0 2px 4px var(--slack-shadow);
  }
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 32px 16px;
  text-align: center;
  color: rgba(255, 255, 255, 0.6);

  p {
    margin: 0 0 16px;
    font-size: 14px;
  }

  button {
    color: white;
  }
}
